
import axios from 'axios';

const state={
    seller:{},
    sellerJusty:false
}
const getters={
    seller(state){
        return state.seller;
    },
    sellerJusty(state){
        return state.sellerJusty;
    }
}

const actions={
    getSeller({commit}){
        axios.get('../../../data.json').then( res => {
            // console.log(res.data.seller.supports[0])
            commit ('getSeller',res.data.seller);
        })
    },
    showDetail({commit}){
        commit('showDetail');
    },
    hideDetail({commit}){
        commit('hideDetail');
    }
}

const mutations={
    getSeller(state,data){
        state.seller=data;
    },
    showDetail(state){
        state.sellerJusty=true;
    },
    hideDetail(state){
        state.sellerJusty=false;
    }
}

export default{
    state,
    getters,
    mutations,
    actions
}


