import axios from 'axios';

const state={
    ratings:[],
    selectRatings : [],
	types:['满意','不满意','全部'],
	selectType : 2,
	onlyContent : false
}

const getters={
    ratings(state){
        return state.ratings;
    },
	selectRatings( state ){
		return state.selectRatings;
	},
	types( state ){
		return state.types;
	},
	selectType( state ){
		return state.selectType;
	},
	onlyContent( state ){
		return state.onlyContent;
	}
}

const actions={
    // 获取评价
    getRatings({commit}){
        axios.get('../../../data.json').then( res => {
            // console.log(res.data.ratings)
            commit ('getRatings',res.data.ratings);
        })
    },
	// 点击筛选内容
	select( {commit} ,{type}){
		commit( "select",{type} );
	},
	// 初始化状态
	changeTypes({commit} ,_types){
		commit( "changeTypes",_types);
	},
	// 只看有内容的评价
	toggle({commit}){
		commit('toggle');
	}
}

const mutations={
    getRatings(state,data){
        state.ratings=data;
        state.selectRatings = data;
    },
    select(state,{type}){
        // console.log(666);
        //点击谁就把谁的状态改变一下
        state.selectType=type;
        state.selectRatings=state.ratings.filter(rating=>{
            if(state.selectType==2){
                return true;
            }else{
                return rating.rateType==state.selectType;
            }
        })
    },
    toggle(state){
        state.onlyContent=!state.onlyContent;
        state.selectRatings = state.ratings.filter( rating =>{
            //   如果  我想看内容   但是  又没有内容  
            if( state.onlyContent && !rating.text ){
                return false;
            }else if( state.selectType === 2 ){
                return true;
            }else{
                //          0 // 1         0   //  1 
                return rating.rateType == state.selectType;
            }
        })
    },
    changeTypes(state,_types){
        state.types = _types;
		state.selectType = 2;
		//		需要遍历的数据			24
		state.selectRatings = state.ratings;
		state.onlyContent = false;
    }
}

export default{
    state,
    getters,
    mutations,
    actions
}


