<template>
    <div class="header">
        <!-- {{seller.name}} -->
        <div class="bg">
            <!-- <img src="../../../static/imgs/bg.png" alt=""> -->
             <img :src="seller.avatar" alt="">
        </div>
        <div class="up">
            <img :src="seller.avatar" alt="">
            <div class="msg">
                <p><img src="../../../static/imgs/brand.png">{{seller.name}}</p>
                <p v-if='seller.supports'>{{seller.description}}/{{seller.deliveryTime}}分钟送达</p>
                <p v-if='seller.supports'><img src="../../../static/imgs/decrease.png">{{seller.supports[0].description}}</p>
            </div> 
            <div class="supports" @click='showDetail'>
                <span v-if='seller.supports'>{{seller.supports.length}}个</span>
                <span>></span>
            </div>         
        </div>
        <div class="down">
            <!-- <img src="../../../static/imgs/bulletin.png">
            <span>{{seller.bulletin}}</span> -->
            <div class="logo">
                <img src="../../../static/imgs/bulletin.png">
            </div>
            <div class="info" @click='showDetail'>
                <span>{{seller.bulletin}}</span>
            </div>
            <span>></span>
        </div>
        <sellerDetail></sellerDetail>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import sellerDetail from '../sellerDetail/sellerDetail.vue';

    export default{
        computed:{
            ...mapGetters([
                'seller'
            ])
        },
        components:{
            sellerDetail
        },
        methods:{
           showDetail(){
               this.$store.dispatch('showDetail');
           }
        }
    }
</script>

<style lang="less" scoped>
    .header{
        width:100%;
        height: 2.68rem;
        background:rgba(7, 17, 27, 0.5);
        box-sizing: border-box;
        position: relative;
        overflow: hidden;
        .bg{
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            z-index: -5;
            filter: blur(0.1rem);
            img{
                height: 100%;
                width: 100%;
            }
        }
        .up{
            height: 2.12rem;
            box-sizing: border-box;
            padding-top: 0.48rem;
            // border: 1px solid red;
            img{
                width: 1.28rem;
                height: 1.28rem;
                margin: 0 0.32rem 0.36rem 0.48rem;
                border-radius: 0.04rem;
                float: left;
            }
            .msg{
                width: 5rem;
                height: 1.28rem;
                float: left;
                p:first-child{
                    text-align: left;
                    color:rgb(255, 255,255);
                    font-size: 0.32rem;
                    line-height: 0.36rem;
                    font-weight: bold;
                    height: 0.4rem;
                    margin-bottom: 0.16rem;
                    img{
                        width: 0.6rem;
                        height: 0.36rem;
                        margin: 0 0.12rem 0 0;
                    }
                }
                p:nth-child(2){
                    color:rgb(255, 255,255);
                    font-size: 0.24rem;
                    line-height: 0.24rem;
                    font-weight: 2rem;
                    margin-bottom: 0.2rem;
                }
                p:last-child{
                    color:rgb(255, 255,255);
                    font-size: 0.2rem;
                    line-height: 0.24rem;
                    font-weight: 2rem;
                    img{
                        width: 0.24rem;
                        height: 0.24rem;
                        margin: 0 0.08rem 0 0;
                    }
                }
            }
            .supports{
                width: 0.88rem;
                height: 0.48rem;
                font-size: 0.2rem;
                color: rgb(255,255, 255);
                font-weight: 0.2rem;
                background-color: rgba(0, 0, 0,0.2);
                position: absolute;
                right: 0.24rem;
                bottom: 0.8rem;
                border-radius: 0.4rem;
                padding-left: 0.16rem;
                box-sizing: border-box;
                span{
                    line-height: 0.48rem;
                }
            }
        }
        .down{
            height: 0.56rem;
            width: 100%;
            box-sizing: border-box;
            // padding-top:0.14rem; 
            background-color: rgba(7, 17, 27, 0.2);
            .logo{
                img{
                    width: 0.6rem;
                    margin:0.14rem 0.08rem 0 0.24rem;
                    height: 50%;
                    // border: 1px solid red;
                    float: left;
                    box-sizing: border-box;
                }
                
            }
            .info{
                width: 80%;
                height: 0.56rem;
                float: left;
                color:rgb(255, 255,255);
                box-sizing: border-box;
                // margin-right: 0.24rem;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                font-size: 0.2rem;
                line-height: 0.56rem;
                font-weight: 2rem;             
            }
            span{
                color:rgb(255, 255,255);
                font-size: 0.2rem;
                line-height: 0.56rem;
                font-weight: 2rem;
                margin:0  0.24rem 0 0.08rem; 
            }
            
        }
        
    }
</style>



