<template>
    <div class="sellerDetail" v-show="sellerJusty">
        <div class="title">
            <p>{{seller.name}}</p>
            <div class="stars">
                <star :score='seller.score'></star>
            </div>
        </div>
        <div class="discount">
            <p class="share">优惠信息</p>
            <p v-if='seller.supports'><img src="../../../static/imgs/decrease.png">{{seller.supports[0].description}}</p>
            <p v-if='seller.supports'><img src="../../../static/imgs/discount.png">{{seller.supports[1].description}}</p>
            <p v-if='seller.supports'><img src="../../../static/imgs/guarantee.png">{{seller.supports[2].description}}</p>
            <p v-if='seller.supports'><img src="../../../static/imgs/invoice.png">{{seller.supports[3].description}}</p>
            <p v-if='seller.supports'><img src="../../../static/imgs/special.png">{{seller.supports[4].description}}</p>
            <!-- <p v-if='seller.supports' :v-for="v in seller.supports">{{v}}</p>  -->
        </div>
        <div class="public">
            <p class="share">商家公告</p>
            <p>{{seller.bulletin}}</p>
        </div>
        <div class="multip" @click="hideDetail">×</div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import star from '../stars/stars.vue'

    export default{
        computed:mapGetters([
            'seller',
            'sellerJusty'
        ]),
        components:{
            star
        },
        methods:{
            hideDetail(){
                this.$store.dispatch('hideDetail');
            }
        }
    }
</script>

<style lang='less' scoped>
    .sellerDetail{
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
        background: rgba(7, 17, 27, 0.8);
        // filter: blur(4px);
        color: rgb(255, 255, 255);
        z-index: 10000;
		.title{
            // border: 1px solid #ccc;
            margin-top: 1.28rem;
            font-size: 0.32rem;
            font-weight: 700;
            line-height: 0.32rem;
            text-align: center;
            .stars{
                margin: 0.32rem 0 0.56rem 0;
                img{
                    height: 0.48rem;
                    margin-right: 0.2rem;
                }
            }
        }
        .discount{
              border-top: 1px solid rgba(255, 255, 255, 0.2);
              margin: 0 0.72rem;
            //   padding: 0.48rem 0 0.56rem 0;
              p{
                font-size: 0.24rem;
                font-weight: 200;
                line-height: 0.24rem;
                height: 0.32rem;
                margin-bottom: 0.24rem;
                img{
                    width: 0.32rem;
                    height: 0.32rem;
                    margin-right: 0.12em;
                    vertical-align: middle;
                }
              } 
            .share{
                position: relative;
                width: 1.2rem;
                top: -0.1rem;
                margin: 0 auto;
                text-align: center;
                background: #394249;
                font-size: 0.28rem;
                font-weight: 700;
                line-height: 0.28rem;
                padding: 0 0.24rem;
                margin-bottom: 0.48em;
            }             
        }
        .public{
            border-top: 1px solid rgba(255, 255, 255, 0.2);
            margin: 0 0.72rem;
            font-size: 0.24rem;
            font-weight: 200;
            line-height: 0.48rem;
            height: 0.32rem;
            .share{
                position: relative;
                width: 1.2rem;
                top: -0.1rem;
                margin: 0 auto;
                text-align: center;
                background: #394249;
                font-size: 0.28rem;
                font-weight: 700;
                line-height: 0.28rem;
                padding: 0 0.24rem;
                margin-bottom: 0.48em;
            }             
        }
        .multip{
            position: absolute;
            bottom: 0.64rem;
            left: 50%;
            font-size: 0.64rem;
            color: rgba(255, 255, 255, 0.5);
        }
	}
</style>



