<template>
    <!-- <div class="cubes">
        <p @click='select(2)' class="one" :class="{'active' : selectType === 2 }">{{types[2]}}<span v-if="ratings">{{ratings.length}}</span></p>
        <p @click='select(0)' class="two" :class="{'active' : selectType === 0 }">{{types[0]}}<span>47</span></p>
        <p @click='select(1)' class="three" :class="{'active' : selectType === 1 }">{{types[1]}}<span>10</span></p>
    </div> -->
    <div class="satisfy">
        <div class="cubes">
            <p @click='select(2)' class="one" :class="{'active' : selectType === 2 }">{{types[2]}}<span v-if="ratings">{{ratings.length}}</span></p>
            <p @click='select(0)' class="two" :class="{'active' : selectType === 0 }">{{types[0]}}<span>{{satisfycount}}</span></p>
            <p @click='select(1)' class="three" :class="{'active' : selectType === 1 }">{{types[1]}}<span>{{unsatisfycount}}</span></p>
        </div>
        <div  class="shrink" @click='toggle'>
            <p v-show="flag"><img src="../../../static/imgs/gou.png">只看有内容的评价</p>
            <p v-show="!flag"><img src="../../../static/imgs/gou1.png">只看有内容的评价</p>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'

    export default{
        computed:{
			...mapGetters([
                'ratings',
				'types',
				'selectType',
				'onlyContent'
            ]),
            satisfycount(){
                return this.ratings.filter(rating=>{
                    return rating.rateType==0;
                }).length;
            },
            unsatisfycount(){
                return this.ratings.filter(rating=>{
                    return rating.rateType==1;
                }).length;
            },
        },
        data(){
            return{
                flag:true
            }
        },
        methods:{
			select( type ){
				this.$store.dispatch('select',{type});
			},
			toggle(){
                this.$store.dispatch('toggle');
                this.flag=!this.flag;
			}
		}
    }
</script>
<style lang='less' scoped>
    //满意度统计
    .satisfy{
        height: 2.34rem;
        border-bottom:0.02rem solid #e5e6e8;
        box-sizing: border-box;
        .cubes{
            border-bottom:0.01rem solid #e5e6e8;
            padding:0.36rem 0;
            margin:0 0.36rem;
            p{
                width: 1.4rem;
                height:0.64rem;
                border-radius: 0.02rem;
                font-size: 0.24rem;
                text-align: center;
                line-height: 0.62rem;
                margin-right:0.16rem;
            }
            span{
                font-size:0.2rem;
                margin-left:0.06rem;
            }
            // 全部
            p:first-child{
                background:#00a0dc;
                color: rgb(255, 255, 255);
                display: inline-block;
                &.active{
                    color:lightcoral;
                }
            }
            // 满意
            p:nth-child(2){
                background:#ccecf7;
                color: #57575f;
                display: inline-block;
                 &.active{
                    color:lightcoral;
                }
            }
            // 不满意
            p:last-child{
                background:#eaebed;
                color: #57575f;
                display: inline-block;
                 &.active{
                    color:lightcoral;
                }
            }
        }
        // 有内容的评价收缩
        .shrink{
            padding-left:0.36rem;
            height:0.98rem;
            color: rgb(147, 153, 159);
            line-height:0.98rem;
            p{
                font-weight:400;
                font-size: 0.24rem;
                img{
                    width: 0.4rem;
                    height: 0.4rem;
                    vertical-align: middle;
                    margin-right: 0.16rem;
                }
            }  
        }
        
    }
</style>




