<template>
    <div class="goods">
        <!-- 左边栏 -->
        <div class="list" ref="list">
            <div>
                <div @click='selectMenu(k)' class="classify" :class='{"current" : currentIndex == k }' v-if="goods[k]" v-for="(v,k) in goods" :key="k">
                    <p>{{v.name}}</p>
                </div>
            </div>
        </div>

        <!-- 右部菜单 -->
        <div class="menus" ref="menus">
            <div>
                <div class="module food-list-hook" v-if="goods[b]" v-for="(a,b) in goods" :key="b">
                    <!-- 标题 -->
                    <div class="title">
                        {{a.name}}
                    </div>
                    <!-- 菜品内容 -->
                    <div class="dishes" v-for="(food,i) in a.foods" :key="i" @click='showgoodsDetail(food)'>
                        <div class="left">
                            <img :src="food.icon">
                        </div>
                        <div class="right">
                            <div class="name">{{food.name}}</div>
                            <div class="descrip">{{food.description}}</div>
                            <div class="count"><span>月售份{{food.sellCount}}</span><span>好评率100%</span></div> 
                            <div class="price">
                                <span>¥{{food.price}}</span>
                                <span>{{food.oldPrice}}</span>
                                <add :food = 'food'></add>
                                <!-- <div class='add'>
                                    <span><img src="../../../static/imgs/add.png"></span>
                                    <span>1</span>
                                    <span><img src="../../../static/imgs/decre.png"></span>
                                </div> -->
                            </div> 
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <goodsDetail></goodsDetail>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    import BScroll from 'better-scroll'
    import star from '../stars/stars.vue'
    import goodsDetail from '../goodsDetail/goodsDetail.vue'
    import add from '../add/add.vue'

    export default{ 
        computed : {
			...mapGetters([
                'goods',
                'getters',
            ]),
            // 计算当前位置在哪一区间内 然后返回当前i
			currentIndex(){
                // console.log(this.heights)
				for( let i = 0 ; i < this.heights.length ; i++){
					let height1	= this.heights[i];
					let height2	= this.heights[i+1];
					if( this.scrollY >= height1 && this.scrollY < height2){
						return i;
					}
				}
				return 0;
			}
        },
        data(){
			return {
				heights:[],
				scrollY : ''
			}
		},
        components:{
            star,
            goodsDetail,
            add,
        },
        mounted(){
            this.$nextTick( () => {
                setTimeout( () => {
                    this.initBScroll();
                    this.calcHeight();
                })
            })
        },
        methods:{
            initBScroll(){
                // 左边栏
				if( !this.listBScroll ){
					this.listBScroll = new BScroll( this.$refs.list,{
                        click : true,
					})
				}else{
					this.listBScroll.refresh();
                };
                // 右边栏
                if( !this.dishesBScroll ){
					this.dishesBScroll = new BScroll( this.$refs.menus,{
                        click : true,
   						probeType : 3
					})
				}else{
					this.dishesBScroll.refresh();
                };
                this.dishesBScroll.on('scroll', (pos) => {
					this.scrollY = Math.abs( Math.round( pos.y))
				}) 
            },
            // 显示详情页面
			showgoodsDetail(food){
				this.$store.dispatch('showgoodsDetail',food);
            },

            calcHeight(){
				let foodList = document.getElementsByClassName('food-list-hook');
				let height = 0 ;
				this.heights.push( height );
				for(let i = 0 ; i < foodList.length ; i++){
                    //获取每一个li（区块）的高度
					height += foodList[i].clientHeight;
                    this.heights.push( height );
                }
				// [0, 599, 684, 769, 968, 1110, 1252, 1451, 1765, 2136]

            },
            // 左侧点击事件
            selectMenu(k){
				console.log(k)
				let foodList = document.getElementsByClassName('food-list-hook');
				this.dishesBScroll.scrollToElement(foodList[k],300);
			},
		}
   } 
</script>
<style lang="less" scoped>
    .goods{
        position: absolute;
        width: 100%;
        top:3.52rem;
        overflow: hidden;
        bottom:1rem;
        display: flex;
        width: 100%;
        //  左边栏
        .list{
            flex: 0 0 1.6rem;
            // border: 1px solid lightcoral;
            width:1.6rem; 
            height: 8rem;
            .classify{
                width: 1.6rem;
                height: 1.08rem;
                font-size: 0.24rem;
                color:rgb(7, 17, 27);
                padding:0 0.24rem;
                box-sizing: border-box;
                background: #f3f5f7;
                // border: 1px solid lightcoral;
                line-height: 0.28rem;
                display: table;
                &.current{
					background: rgb(255, 255, 255);
				}
                p{  
                    width: 1.12rem;
                    // height: 100%;
                    // line-height: 1.08rem;
                    box-sizing: border-box;
                    display: table-cell;
                    vertical-align: middle;
                    // border: 1px solid lightgreen;
                    vertical-align: middle;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                }
            }
        }
        // 右部菜单
        .menus{
            flex: 1;
            height: 100%; 
            // border: 1px solid lightseagreen;
            .title{
                height: 0.54rem;
                background:#f3f5f7;
                color: #91969a;
                font-size: 0.24rem;
                line-height: 0.54rem;
                padding-left: 0.28rem;
                border-left: 2px solid #d9dde1;
                // border: 1px solid lightcoral;
            }
            .dishes{
                margin: 0 0.36rem;
                padding: 0.36rem 0;
                display: flex;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                .left{
                    margin-right:0.2rem; 
                    img{
                        width: 1.16rem;
                        height: 1.16rem;
                    }
                }
                .right{
                    // border: 1px solid lightseagreen;
                    width: 3.8rem;
                    .name{
                        font-size: 0.28rem;
                        color: rgb(7, 17, 27);
                        line-height: 0.28rem;
                    }
                    .count,.descrip{
                        font-size: 0.2rem;
                        color: rgb(147, 153, 159);
                        line-height: 0.2rem;
                        margin-top: 0.16rem;
                    }
                    .count{
                        span:first-child{
                            margin-right: 0.24rem;
                        }
                    }
                    .price{
                        font-size: 0.28rem;
                        color: #eb121d;
                        font-weight: 700;
                        line-height: 0.48rem;
                        span:nth-child(2){
                            font-size: 0.2rem;
                            color: rgb(147, 153, 159);
                        }
                        .add{
                            float: right;
                            span{
                                &:nth-child(2){
                                    font-size: 0.2rem;
                                    color: rgb(147, 153, 159);
                                    line-height: 0.48rem;
                                    padding:0 0.12rem;
                                    // border: 1px solid red;
                                }
                            }
                            img{
                                width: 0.4rem;
                                height: 0.4rem;
                                vertical-align: middle;
                                margin-right:0; 
                                // border: 1px solid red;
                            }
                        }
                    }
                }
            }
        }
    }
</style>




