<template>
   <div class='add'>
        <span @click.stop='reduce' v-show="food.count>0"><img src="../../../static/imgs/decre.png"></span>
        <span v-show="food.count>0">{{food.count}}</span>
        <span @click.stop='add'><img src="../../../static/imgs/add.png"></span>
    </div>
</template>

<script>
    import Vue from 'vue'
    export default{
        props:['food'],
		methods:{
			add(){
				if( !this.food.count ){
					Vue.set(this.food , 'count' , 1);
				}else{
					this.food.count ++;
				}
			},
			reduce(){
                console.log(111);
				if( this.food.count ){
					this.food.count --;
				}
			}
		}
   } 
</script>

<style lang='less' scoped>
    .add{
        // float: right;
        span{
            &:nth-child(2){
                font-size: 0.2rem;
                color: rgb(147, 153, 159);
                line-height: 0.48rem;
                padding:0 0.12rem;
                // border: 1px solid red;
            }
        }
        img{
            width: 0.4rem;
            height: 0.4rem;
            vertical-align: middle;
            margin-right:0; 
            // border: 1px solid red;
        }
    }
</style>



