<template>
  <div id="app">
    <tophead></tophead>
    <div class="tab">
        <div class="tab-item">
            <router-link to="/goods">商品</router-link>
        </div>
        <div class="tab-item">
            <router-link to="/ratings">评价</router-link>
        </div>
        <div class="tab-item">
            <router-link to="/seller">商家</router-link>
        </div>
    </div>
    <router-view/>
    <!-- <div class="choice">
        <goodchoices></goodchoices>
    </div> -->
    <div class="car">
        <shopcar></shopcar>
    </div>
  </div>
</template>

<script>
import  tophead from './components/tophaed/tophead.vue'
import shopcar from './components/shopCar/shopCar.vue'
import goodchoices from './components/goodChoices/goodChioces'

export default {
  components:{
      tophead,
      shopcar,
      goodchoices
  },
  mounted(){
    this.$store.dispatch('getSeller');
    this.$store.dispatch('getRatings');
    this.$store.dispatch('getGoods');
  }
}
</script>

<style lang="less" scoped>
   #app{
        .tab{
            width: 100%;
            height: 0.8rem;
            background-color: rgb(255, 255, 255);
            font-size: 0.28rem;
            color:rgb(240,20,20);
            line-height: 0.8rem;
            display: flex;
            border-bottom:1px solid rgba(7,17,27,0.1);
            .tab-item{
                flex:1;
                text-align: center;
                a{
                    display: block;
                    &.active{
                        color:red;
                    }
                }
            }
        }

        .car{
            position: fixed;
            bottom: 0;
            width: 100%;
            height: 1rem;
            // background-color: #141d27;
        }

        .choice{
            // height:6.11rem;
            height: 100%;
            position: absolute;
            bottom: 0;
            width: 100%;
        }
    }
</style>



