<template>
	<div class="foodDetail" v-show='detailJusty' ref="goodsdetail">
        <div>
            <!-- 第一块 -->
            <div class="intro">
                <!-- 图片 -->
                <div class="picture">
                    <img :src="food.image">
                </div>
                <!-- 图片下部 -->
                <div class='pad'>
                    <div class="name">{{food.name}}</div>
                    <div class="count"><span>月售份{{food.sellCount}}</span><span>好评率100%</span></div> 
                    <div class="price">
                        <span>¥{{food.price}}</span>
                        <span>{{food.oldPrice}}</span>
                        <!-- 购物车 -->
                        <div v-show="!flag" class='dadd' @click='doadd(flag)'><p>加入购物车</p></div>
                        <!-- 加减 -->
                        <add v-show="flag" :food="food"></add>
                    </div> 
                </div>
            </div>
            <!-- 空白灰色部分 -->
            <div class="grey">

            </div>
            <!-- 商品介绍 -->
            <div class="goodsinfo">
                <p>商品介绍</p>
                <div>
                    {{food.info}}
                </div>
            </div>
            <!-- 空白灰色部分 -->
            <div class="grey">

            </div>
            <!-- 满意度统计 -->
            
            <div class="satisfy">
                <satisfy></satisfy>
                <!-- <div class="cubes">
                    <p>全部<span v-if="food.ratings">{{food.ratings.length}}</span></p>
                    <p>满意<span>47</span></p>
                    <p>不满意<span>10</span></p>
                </div>
                <div  class="shrink">
                    <p><img src="../../../static/imgs/gou.png">只看有内容的评价</p>
                </div> -->
            </div>
            <!-- 评价 -->
            <div class="comments" v-if="food.ratings[k]" v-for="(v,k) in food.ratings" :key="k">
                <!-- 上部信息 -->
                <div class="upper">
                    <!-- 时间 -->
                    <p>{{v.rateTime | dataFormat("y-m-d h:mm:s")}}</p>
                    <!-- 用户 -->
                    <div class="user">
                        <!-- 用户名 -->
                        <p>{{v.username}}</p>
                        <!-- 头像 -->
                        <p><img :src="v.avatar"></p>
                    </div>
                </div>

                <!-- 下部评论 -->
                <div class="comment">
                     <!-- 赞 -->
                    <div><img src="../../../static/imgs/zan.png"></div>
                    <p>{{v.text}}</p>
                </div>
            </div>
        </div>
	</div>
</template>

<script>
	
    import { mapGetters } from 'vuex'
    import BScroll from 'better-scroll'
    import star from '../stars/stars.vue'
    import satisfy from '../satisfy/satisfy.vue'
    import add from '../add/add.vue'

	export default {

		computed : {
			...mapGetters([
				'detailJusty',
				'food'
			])
        },
        data(){
            return{
                flag:false
            }
        },
        components:{
            star,
            satisfy,
            add
        },
        filters:{
            dataFormat:function(num){
                var time = new Date(num);
                var y = time.getFullYear();//年
                var m = time.getMonth() + 1;//月
                var d = time.getDate();//日
                var h = time.getHours();//时
                var mm = time.getMinutes();//分
                var s = time.getSeconds();//秒
                return y+"-"+m+"-"+d+" "+h+":"+mm+":"+s;
            }
        },
        mounted(){
            this.$nextTick( () => {
				if( !this.goodsdetailSCroll ){
					this.goodsdetailSCroll = new BScroll( this.$refs.goodsdetail ,{
                        click:true
					});
				}else{
					this.goodsdetailSCroll.refresh();
				}
            });
            this.$store.dispatch('changeTypes',['推荐','吐槽','全部']);
        },
        methods:{
            doadd(flag){
                // console.log(this.flag)
                this.flag=true;
            }
        }
	}

</script>

<style lang='less' scoped>
	.foodDetail{
        width: 100%;
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
        left: 0;
        background-color: rgb(255, 255, 255);
        // 顶部
        .intro{
            border-bottom:0.02rem solid #e5e6e8;
             // 图片
            .picture{
                position: relative;
                width: 100%;
                height: 0;
                padding-top: 100%;
                img{
                    height:7.5rem;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                }
            }
            // 图片下部
            .pad{
                padding: 0.36rem;
                .name{
                    font-size: 0.28rem;
                    font-weight: 700;
                    color: rgb(7, 17, 27);
                    line-height: 0.28rem;
                    margin-bottom: 0.16rem;
                }
                .count{
                    font-size: 0.2rem;
                    color: rgb(147, 153, 159);
                    line-height: 0.2rem;
                    margin-bottom: 0.36rem;
                    span:first-child{
                        margin-right: 0.24rem;
                    }
                }
                .price{
                        font-size: 0.28rem;
                        color: #eb121d;
                        font-weight: 700;
                        line-height: 0.48rem;
                        span:nth-child(2){
                            font-size: 0.2rem;
                            color: rgb(147, 153, 159);
                        }
                        // 加入购物车
                        .dadd{
                            float: right;
                            width: 1.48rem;
                            height: 0.48rem;
                            border-radius: 0.24rem;
                            padding: 0.12rem 0.24rem;
                            box-sizing: border-box;
                            background-color: rgb(0, 160, 220);
                            p{
                                font-size: 0.2rem;
                                color: rgb(255, 255, 255);
                                line-height: 0.24rem;
                                width: 1.48rem;
                                // vertical-align: top;
                            }
                        }
                        .add{
                            float: right;
                            width: 1.48rem;
                            height: 0.48rem;
                            // span{
                            //     &:nth-child(2){
                            //         font-size: 0.2rem;
                            //         color: rgb(147, 153, 159);
                            //         line-height: 0.48rem;
                            //         padding:0 0.12rem;
                            //         // border: 1px solid red;
                            //     }
                            // }
                            // img{
                            //     width: 0.4rem;
                            //     height: 0.4rem;
                            //     vertical-align: middle;
                            //     margin-right:0; 
                            //     // border: 1px solid red;
                            // }
                        }
                    }
            }
           
        }
        // 商品介绍
        .goodsinfo{
            padding: 0.36rem;
            p{
                font-size:0.28rem;
                color:rgb(7,17,27);
                line-height:0.28rem;
            }
            div{
                padding: 0.12rem 0.16rem 0;
                font-size: 0.24rem;
                font-weight: 200;
                color: rgb(77,85,93);
                line-height: 0.48rem;
            }
        }
        //空白灰色部分
        .grey{
            height: 0.34rem;
            background:#f4f5f7;
            border-bottom:0.02rem solid #e5e6e8;
        }
        // 满意度统计
        .satisfy{
            height: 2.34rem;
            border-bottom:0.02rem solid #e5e6e8;
            box-sizing: border-box;
            .cubes{
                border-bottom:0.01rem solid #e5e6e8;
                padding:0.36rem 0;
                margin:0 0.36rem;
                p{
                    width: 1.4rem;
                    height:0.64rem;
                    border-radius: 0.02rem;
                    font-size: 0.24rem;
                    text-align: center;
                    line-height: 0.62rem;
                    margin-right:0.16rem;
                }
                span{
                    font-size:0.2rem;
                    margin-left:0.06rem;
                }
                // 全部
                p:first-child{
                    background:#00a0dc;
                    color: rgb(255, 255, 255);
                    display: inline-block;
                }
                // 满意
                 p:nth-child(2){
                    background:#ccecf7;
                    color: #57575f;
                    display: inline-block;
                }
                // 不满意
                 p:last-child{
                    background:#eaebed;
                    color: #57575f;
                    display: inline-block;
                }
            }
            // 有内容的评价收缩
            .shrink{
                padding-left:0.36rem;
                height:0.98rem;
                color: rgb(147, 153, 159);
                line-height:0.98rem;
                p{
                    font-weight:400;
                    font-size: 0.24rem;
                    img{
                        width: 0.4rem;
                        height: 0.4rem;
                        vertical-align: middle;
                        margin-right: 0.16rem;
                    }
                }  
            }
            
        }
        // 评价块
        .comments{
            padding: 0.32rem 0;
            margin: 0 0.36rem;
            border-bottom:0.01rem solid #e5e6e8;
            // 上部信息
            .upper{
                // display: flex;
                height: 0.24rem;
                p{
                    float: left;
                    font-size: 0.2rem;
                    color: rgb(147, 153, 159);
                    line-height: 0.24rem;
                }
                .user{
                    float: right;
                    p:first-child{
                        font-size: 0.2rem;
                        color: rgb(147, 153, 159);
                        line-height: 0.24rem;
                        margin-right: 0.12rem;
                    }
                    img{
                        width: 0.24rem;
                        height: 0.24rem;
                        border-radius: 1rem;
                    }
                }
            }
            // 下部评论
            .comment{
                display: flex;
                margin-top: 0.12rem;
                img{
                    width: 0.24rem;
                    height: 0.24rem;
                    vertical-align: middle;
                }
                p{
                    line-height: 0.24rem;
                    margin-left: 0.08rem;
                }
            }
        }
	}
</style>


