<template>
    <div class="goodChoices">
        <div class='cc'></div>
        <div class='bg'>
                <!-- 购物车 -->
            <div class='firstline'>
                <span>购物车</span>
                <span @click='clearcar(foodcar)'>清空</span>
            </div>
             <div ref='scr' class="scr">
                 <div>
                    <div class="container" v-for="(v,k) in foodcar" :key="k">
                        <div class="name">{{v.name}}</div>
                        <div class="right">
                            <div class="price">¥{{v.price}}</div>
                            <add :food='v'></add>
                            <!-- <div class='add'>
                                <span><img src="../../../static/imgs/add.png"></span>
                                <span>1</span>
                                <span><img src="../../../static/imgs/decre.png"></span>
                            </div> -->
                        </div>  
                    </div>
                </div> 
             </div>
            <!-- <div class="car">
                <shopcar></shopcar>
            </div> -->
        </div>   
   </div>
</template>

<script>
    import add from '../add/add.vue'
    import BScroll from 'better-scroll'
    import shopcar from '../shopCar/shopCar.vue'

    export default{
        components:{
            add,
            shopcar
        }, 
        props:['foodcar'],
        mounted(){
            this.$nextTick( () => {
				if( !this.scrSCroll ){
					this.scrSCroll = new BScroll( this.$refs.scr ,{
                        click:true
					});
				}else{
					this.scrSCroll.refresh();
				}
			});
        },
        methods:{
            clearcar(foodcar){
                this.foodcar.forEach((food)=>{
                    food.count=0;
                })
                this.$store.dispatch('clearcar',foodcar);
            }
        }
    }
</script>

<style lang="less" scoped>
    .goodChoices{
        background-color: rgba(7, 17, 27, 0.6);        
        .cc{
            position: fixed;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            // background: #000;
            background-color: rgba(7, 17, 27, 0.6);    
            // filter: blur(10px);
        }
        .bg{
            // filter: blur(10px);
            width: 100%;
            box-sizing: border-box; 
            position: fixed;
            // position: absolute;
            bottom: 1rem;
            background-color: rgb(255, 255, 255);
            // height:0.8rem;
            overflow: hidden;
            .firstline{
                width: 100%;
                height: 0.8rem;
                padding: 0 0.36rem;
                box-sizing: border-box;
                background: #f3f5f7;
                border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
                span:first-child{
                    float: left;
                    font-size: 0.28rem;
                    font-weight: 200;
                    color: rgb(7, 17, 27);
                    line-height: 0.8rem;
                }
                span:last-child{
                    float: right;
                    font-size: 0.24rem;
                    color: rgb(0, 160, 220);
                    line-height: 0.8rem;
                }
            }
            .scr{
                width: 100%;
                max-height : 4.2rem;
                overflow: hidden;
                .container{
                    padding: 0.24rem 0;
                    margin: 0 0.36rem;
                    height: 0.96rem;
                    box-sizing: border-box;
                    border-bottom: 0.01rem solid rgba(7, 17, 27,0.1);
                    .name{
                        float: left;
                        font-size: 0.28rem;
                        color: rgb(7, 17, 27);
                        line-height: 0.48rem;
                    }
                    .right{
                        float: right;
                        display: flex;
                        .price{
                            font-size: 0.28rem;
                            font-weight: 700;
                            color: rgb(240,20, 20);
                            line-height: 0.48rem;
                            margin-right: 0.24rem;
                        }
                        .add{
                            // float: right;
                            span{
                                &:nth-child(2){
                                    font-size: 0.2rem;
                                    color: rgb(147, 153, 159);
                                    line-height: 0.48rem;
                                    padding:0 0.12rem;
                                    // border: 1px solid red;
                                }
                            }
                            img{
                                width: 0.4rem;
                                height: 0.4rem;
                                vertical-align: middle;
                                margin-right:0; 
                                // border: 1px solid red;
                            }
                        }
                    }
                }
            }
          
            // .car{
            //     // position: absolute;
            //     // bottom: 0;
            //     width: 100%;
            //     height: 1rem;
            //     background-color: pink;
            // }
        }
        
    }
</style>



