<template>
    <div class="seller" ref="seller">
        <div>
        <!-- 顶部 -->
        <div class="top">
             <!-- 月售单数、收藏  -->
            <div class="upper">
                 <!-- 左部  -->
                <div class="left">
                    <p>{{seller.name}}</p>
                     <!-- 星星的一排 -->
                    <div class="monthcount">
                        <div class="stars">
                            <star :size='"small"' :score='seller.score'></star>
                            <!-- <img src="../../../static/imgs/star_on.png">
                            <img src="../../../static/imgs/star_on.png">
                            <img src="../../../static/imgs/star_on.png">
                            <img src="../../../static/imgs/star_on.png">
                            <img src="../../../static/imgs/star_off.png"> -->
                        </div>
                        <span>(661)</span>
                        <span>月售690单</span>
                    </div>			
                </div>
                 <!-- 右部爱心收藏  -->
                <div class="heart">
                    <p><img src="../../../static/imgs/heart.png"></p>
                    <p>已收藏</p>
                </div>
            </div>

             <!-- 配送数字统计  -->
            <div class="send">
                <div class="share">
                    <p>起送价</p>
                    <p>{{seller.minPrice}}<span>元</span></p>
                </div>
                <div class="share">
                    <p>商家配送</p>
                    <p>{{seller.deliveryPrice}}<span>元</span></p>
                </div>
                <div class="share">
                    <p>平均配送时间</p>
                    <p>{{seller.deliveryTime}}<span>分钟</span></p>
                </div>
            </div>
        </div>

        <!-- 空白灰色部分 -->
        <div class="grey">

        </div>

        <!-- 公告与活动 -->
        <div class="announce">
            <p class="title">公告与活动</p>
             <!-- 简介  -->
            <div class="intro">
                {{seller.bulletin}}
            </div>
            <!-- 优惠活动 -->
            <div v-if="seller.supports" class="activity">
                <p v-for="(x,y) in seller.supports" :key="y">
                    <img src="../../../static/imgs/decrease.png">{{x.description}}
                </p>
            </div>
        </div>

         <!-- 空白灰色部分 -->
        <div class="grey">

        </div>

        <!-- 商家实景 -->
        <div class="surface" v-if="seller.pics">
            <p class="title">商家实景</p>
            <div ref='imgcontain'>
                <p class="imgs" ref='imgs'>
                    <img v-for="(v,k) in seller.pics" :key="k" :src="v">
                </p>
            </div>
        </div>

        <!-- 空白灰色部分 -->
        <div class="grey">

        </div>

        <!-- 商家信息 -->
        <div v-if="seller.infos" class="info">
            <p class="title">商家信息</p>
            <p class="infolist" v-for="(a,b) in seller.infos" :key="b" >
                {{a}}
            </p>
        </div>

        </div>

    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
	import BScroll from 'better-scroll'
    import star from '../stars/stars.vue'

	export default{
		computed : {
			...mapGetters([
				'seller'
			])
        },
        components:{
            star
        },
		watch:{
            seller(){
                this.$nextTick( () => {
                    this._initPicture();
                })
            }
		},
		mounted(){
			this.$nextTick( () => {
				if( !this.sellerSCroll ){
					this.sellerSCroll = new BScroll( this.$refs.seller ,{
					});
				}else{
					this.sellerSCroll.refresh();
				}
			});
			this._initPicture();
		},
		methods:{
			_initPicture(){
				if( this.seller.pics){
					let pw = 240;
					let m = 12;
					let w = ( pw + m ) * this.seller.pics.length - m;
					this.$refs.imgs.style.width = w/100 + 'rem';

					if( !this.picBScroll ){
						this.picBScroll = new BScroll( this.$refs.imgcontain ,{
							scrollX : true ,
							eventPassthrough : 'vertical'
						})
					}else{
						this.picBScroll.refresh();
					}
				}

			}
		}
	}
</script>
<style lang="less" scoped>
    .seller{
        height: 6rem;
        position: absolute;
        z-index: -55;
        width: 100%;
         // 标题公共样式
        .title{
            font-size:0.28rem;
            color:rgb(7,17,27);
            line-height:0.28rem;
        }
        // 顶部
        .top{
            height:3.02rem;
            // width:100%;
            border-bottom:0.01rem solid #e5e6e8;
            padding:0.36rem;
            box-sizing: border-box;
            .upper{
                height:50%;
                width:100%;
                box-sizing: border-box;
                // border: 1px solid yellow;
                border-bottom:0.01rem solid #e5e6e8;
                .left{
                    float:left;
                    width:4.4rem;
                    // border: 1px solid yellow;
                    p{
                        font-size:0.28rem;
                        color:rgb(7,17,27);
                        line-height:0.28rem;
                        margin-bottom:0.16rem;
                    }
                    .monthcount{
                        display:flex;
                        .stars{
                            width:2rem;
                            height:0.36rem;
                            margin-right:0.16rem;
                            img{
                                width:0.36rem;
                                height:0.36rem;
                            }
                        }
                        span{
                            font-size:0.2rem;
                            color:rgb(77,85,93);
                            line-height:0.36rem;
                        }
                        span:last-child{
                            margin-left:0.24rem;
                        }
                    }
                }
                .heart{
                    float:right;
                    // border: 0.01rem solid red;
                    width:0.64rem;
				    height:0.76rem;
                    box-sizing: border-box;
                    p:first-child{
                        img{
                            width: 0.74rem;
                            height: 0.48rem;
                        }
                        margin-bottom:0.08rem;
                    }
                    p:last-child{
                        font-size: 0.2rem;
                        color:rgb(77,85,93);
                        line-height:0.2rem;
                    }
                }
            }
           .send{
                width:100%;
                height:1.5rem;
                box-sizing: border-box;
                display:flex;
                text-align:center;
                padding:0.36rem 0;
                div:first-child{
                    border-right:1px solid rgba(7,17,27,0.1);
                }
                div:nth-child(2){
                    border-right:1px solid rgba(7,17,27,0.1);
                }
                .share{
                    // border: 1px solid lightcoral;
                    width:33%;
                    //border-right:1px solid rgba(7,17,27,0.1);
                    p:first-child{
                        font-size: 0.2rem;
                        color:rgb(147,153,159);
                        line-height:0.2rem;
                        margin-bottom: 0.08rem;
                    }
                    p:last-child{
                        font-size: 0.48rem;
                        color:rgb(7,17,27);
                        line-height:0.48rem;
                        span{
                            font-size: 0.2rem;
                        }
                    }
                }
            }

        }
         //空白灰色部分
        .grey{
            height: 0.34rem;
            background:#f4f5f7;
            border-bottom:0.02rem solid #e5e6e8;
        }

        .announce{
            padding:0.36rem;
            // >P{
            //     font-size:0.28rem;
            //     color:rgb(7,17,27);
            //     line-height:0.28rem;
            // }
            .intro{
                padding:0.16rem 0.24rem 0.32rem 0.24rem;
                font-size:0.24rem;
                font-weight:200;
                color:rgb(240,20,20);
                line-height:0.48rem;
            }
            .activity{
                // border: 1px solid lightblue;
                >p{
                    padding:0.32rem 0.24rem;
                    font-size:0.24rem;
                    font-weight:200;
                    color:rgb(7,17,27);
                    line-height:0.48rem;
                    border-top:0.01rem solid rgba(7,17,27,0.1);
                    img{
                        width:0.32rem;
                        height:0.32rem;
                        vertical-align:middle;
                    }
                }
                
            }
        }
        // 实景
        .surface{
            padding:0.36rem;
            div{
                width: 7rem;
				white-space: nowrap;
                overflow: hidden;
                .imgs{
                    //border: 1px solid pink;
                    margin-top:0.24rem;
                    height:1.82rem;
                    display:flex;
                    // display: inline-block;
                    // white-space: nowrap;
                    // overflow: hidden;
                    img{
                        float:left;
                        //border: 1px solid lightgreen;
                        width:2.4rem;
                        height:1.8rem;
                        margin-right:0.12rem;
                        &:last-child{
                            margin: 0;
                        }
                    }
                }	
            }
		}

        .info{
            padding:0.36rem;
            p:first-child{
                margin-bottom:0.24rem;
            }
            .infolist{
                border-top:1px solid rgba(7,17,27,0.1);
                padding:0.32rem 0.24rem;
                font-size:0.24rem;
				font-weight:200;
				color:rgb(7,17,27);
				line-height:0.32rem;
            }
            
		}

    }
</style>


