<template>
  <div class="stars" :class='starSize'>
      <span v-for='(itemClass,k) in starsArr' :key="k" :class='itemClass' class="starSty"></span>  
  </div>
</template>
<script>
	const LENGTH = 5;
	const ON   = 'on';
	const OFF  = 'off';
	const HALF = 'half'

	export default{
		props : [
			'score',
			'size'
		],
		computed : {
			starSize(){
				return 'star-'+this.size;
			},
			starsArr(){
				// 定义空数组
				let result = [];
				// 取整 3.6 this.scroe
				let integer = Math.floor( this.score ); //3
				let decimal = this.score - integer;		//0.6

				for(let i =0 ; i < integer ;i++){
					result.push( ON );
					// result[ON,ON,ON]
				}
				if( decimal > 0.5 ){
					result.push( HALF );
					// result[ON,ON,ON,HALF]
				}
				while( result.length < LENGTH ){
					result.push( OFF );
					// result[ON,ON,ON,HALF,OFF]
				}
				return result;
			}
		}

	}
</script>
<style lang="less" scoped>
    .stars{
		// border: 1px solid greenyellow;
        &.star-small{
			width:2.2rem !important;
			padding: 0 !important;
			.starSty{
				display: inline-block;
				width: 0.3rem;
				height: 0.3rem;
				margin-right: 0.1rem;
				&.on{
					background-image: url('star_on.png');
					background-size: 0.3rem 0.3rem;
					background-repeat: no-repeat;
				}
				&.half{
					background-image: url('star_half.png');
					background-size: 0.3rem 0.3rem;
					background-repeat: no-repeat;
				}
				&.off{
					background-image: url('star_off.png');
					background-size: 0.3rem 0.3rem;
					background-repeat: no-repeat;
				}
			}
		}
		&.star-least{
			.starSty{
				display: inline-block;
				width: 0.2rem;
				height: 0.2rem;
				margin-right: 0.04rem;
				&.on{
					background-image: url('star_on.png');
					background-size: 0.2rem 0.2rem;
					background-repeat: no-repeat;
				}
				&.half{
					background-image: url('star_half.png');
					background-size: 0.2rem 0.2rem;
					background-repeat: no-repeat;
				}
				&.off{
					background-image: url('star_off.png');
					background-size: 0.2rem 0.2rem;
					background-repeat: no-repeat;
				}
			}
		}
		.starSty{
			display: inline-block;
			width: 0.4rem;
			height: 0.4rem;
			margin-right: 0.1rem;
			&.on{
				background-image: url('star_on.png');
				background-size: 0.4rem 0.4rem;
				background-repeat: no-repeat;
			}
			&.half{
				background-image: url('star_half.png');
				background-size: 0.4rem 0.4rem;
				background-repeat: no-repeat;
			}
			&.off{
				background-image: url('star_off.png');
				background-size: 0.4rem 0.4rem;
				background-repeat: no-repeat;
			}
		}
    }
</style>



