<template>
    <div class="ratings" ref="ratings">
        <div>
            <!-- 综合评分数-->
            <div class="summary">
                <!--左部-->
                <div class="score">
                    <p>3.9</p>
                    <p>综合评分</p>
                    <p>高于周边商家69.2%</p>
                </div>
            <!--右部星级评分-->
                <div class="stars">
                    <div>
                        <p>服务态度</p>
                        <div class="imgs">
                            <div class='line'>
                                <star :score='3.9' :size='"small"'></star>
                            </div>
                            <span>3.9</span>   
                        </div>
                    </div>
                    <div>
                        <p>服务态度</p>
                        <div class="imgs">
                            <div class='line'>
                                <star :score='4.0' :size='"small"'></star>
                            </div>
                            <span>4.0</span>   
                        </div>    
                    </div>
                    <div>
                        <p>送达时间</p>
                        <span>44分钟</span>
                    </div>   
                </div>
            </div>

            <!-- 空白灰色部分 -->
            <div class="grey">

            </div>
            <!-- 满意度统计 -->
            <div class="satisfy">
                <satisfy></satisfy>
                <!-- <div class="cubes">
                    <p>全部<span v-if="ratings">{{ratings.length}}</span></p>
                    <p>满意<span>47</span></p>
                    <p>不满意<span>10</span></p>
                </div>
                <div  class="shrink">
                    <p><img src="../../../static/imgs/gou.png">只看有内容的评价</p>
                </div> -->
            </div>
            <!-- 评价 -->
            <div class="comments" v-if="selectRatings[k]" v-for="(v,k) in selectRatings" :key="k">
                <!-- 左部头像 -->
                <div class="headpic">
                    <img :src="v.avatar">
                </div>

                <!-- 右部 -->
                <div class="rightpart">
                    <!-- 用户名和时间 -->
                    <div class="nametime">
                        <span>{{v.username}}</span>
                        <span>{{v.rateTime | dataFormat("y-m-d h:mm:s")}}</span>
                    </div>
                    
                    <!-- 用户下部 -->
                    <div class="below">
                        <!-- 星级 -->
                        <div class="level">
                            <div class="stars">
                                <star :score='v.score' :size='"least"'></star>
                            </div>
                            <!-- 送达时间 -->
                            <span>{{v.deliveryTime}}分钟送达</span>
                        </div>
                        <!-- 评语 -->
                        <div class="comment">
                            {{v.text}}
                        </div>
                        <!-- 赞 -->
                        <div class="praise">
                            <p><img src="../../../static/imgs/zan.png"></p>
                            <div>
                                <div class="item"v-if="v.recommend[b]" v-for="(a,b) in v.recommend" :key="b">
                                    {{a}}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <!-- 星星 -->
            <!-- <star :score='2.6' :size='"small"'></star> -->
        </div>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    import BScroll from 'better-scroll'
    import star from '../stars/stars.vue'
    import satisfy from '../satisfy/satisfy.vue'

    export default{
        computed : {
			...mapGetters([
                'ratings',
                'seller',
                'selectRatings'
			])
        },
        components:{
            star,
            satisfy
        },
        // watch:{
        //     ratings(){
        //         this.$nextTick(()=>{
        //             if(!this.ratingsSCroll){
        //                 this.ratingsSCroll=new BScroll(document.getElementById('ratings'),{});
        //             }else{
        //                 this.ratingsSCroll.refresh();
        //             }
        //         })
        //     }
        // },
        filters:{
            dataFormat:function(num){
                var time = new Date(num);
                var y = time.getFullYear();//年
                var m = time.getMonth() + 1;//月
                var d = time.getDate();//日
                var h = time.getHours();//时
                var mm = time.getMinutes();//分
                var s = time.getSeconds();//秒
                return y+"-"+m+"-"+d+" "+h+":"+mm+":"+s;
            }
        },
        mounted(){
            this.$nextTick( () => {
				if( !this.ratingsSCroll ){
					this.ratingsSCroll = new BScroll( this.$refs.ratings ,{
                        click:true
					});
				}else{
					this.ratingsSCroll.refresh();
				}
            });
            // this.$store.dispatch('changeTypes',['推荐','吐槽','全部']);
        }
    }
</script>
<style lang="less" scoped>
    .ratings{
        width: 100%;
        height: 6rem;
        position: absolute;
        z-index: -55;
        // 综合评分数
        .summary{
            // border-top:0.01rem solid #e5e6e8;
            border-bottom:0.02rem solid #e5e6e8;
            box-sizing: border-box;
            height: 2.14rem;
            display: flex;
            padding: 0.36rem;
            // 左部综合评分
            .score{
                width: 2.75rem;
                border-right: 1px solid #e5e6e8;
                box-sizing: border-box;
                text-align: center;
                p:first-child{
                   font-size: 0.48rem;
                   color: rgb(255, 153, 0);
                   line-height: 0.56rem; 
                   margin-bottom: 0.12rem;
                }
                p:nth-child(2){
                   font-size: 0.24rem;
                   color: rgb(7, 17, 27);
                   line-height: 0.24rem; 
                   margin-bottom: 0.16rem;
                }
                p:last-child{
                   font-size: 0.2rem;
                   color: #92979a;
                   line-height: 0.2rem; 
                }
            }
            // 右部星星
            .stars{
                width: 4.74rem;
                box-sizing: border-box;
                font-size: 0.24rem;
                padding-left: 0.48rem;
                p{
                    width: 1rem;
                    color: rgb(7, 17, 27);
                    line-height: 0.36rem;
                    margin-bottom: 0.16rem;
                    display: inline-block;
                } 
                .imgs{
                    width: 2.2rem;
                    height: 0.26rem;
                    display: inline-block;
                    vertical-align: top;
                    .line{
                        width:2.8rem;
                        float: left;
                    }
                    span{
                        position: absolute;
                        right: 0.48rem;
                        float: left;
                        margin-top:0.05rem; 
                    }
                }
                span{
                    color: rgb(147, 153, 159);
                }
            }
        }

        //空白灰色部分
        .grey{
            height: 0.34rem;
            background:#f4f5f7;
            border-bottom:0.02rem solid #e5e6e8;
        }
        //满意度统计
        .satisfy{
            height: 2.34rem;
            border-bottom:0.02rem solid #e5e6e8;
            box-sizing: border-box;
            .cubes{
                border-bottom:0.01rem solid #e5e6e8;
                padding:0.36rem 0;
                margin:0 0.36rem;
                p{
                    width: 1.4rem;
                    height:0.64rem;
                    border-radius: 0.02rem;
                    font-size: 0.24rem;
                    text-align: center;
                    line-height: 0.62rem;
                    margin-right:0.16rem;
                }
                span{
                    font-size:0.2rem;
                    margin-left:0.06rem;
                }
                // 全部
                p:first-child{
                    background:#00a0dc;
                    color: rgb(255, 255, 255);
                    display: inline-block;
                }
                // 满意
                 p:nth-child(2){
                    background:#ccecf7;
                    color: #57575f;
                    display: inline-block;
                }
                // 不满意
                 p:last-child{
                    background:#eaebed;
                    color: #57575f;
                    display: inline-block;
                }
            }
            // 有内容的评价收缩
            .shrink{
                padding-left:0.36rem;
                height:0.98rem;
                color: rgb(147, 153, 159);
                line-height:0.98rem;
                p{
                    font-weight:400;
                    font-size: 0.24rem;
                    img{
                        width: 0.4rem;
                        height: 0.4rem;
                        vertical-align: middle;
                        margin-right: 0.16rem;
                    }
                }  
            }
            
        }

        //评论区
        .comments{
            //  height:2.62rem;
             box-sizing:border-box;
             border-bottom:0.01rem solid #e5e6e8;
             display:flex;
             padding-top: 0.36rem;
            //  padding:0.36rem 0;
            //  左部头像
            .headpic{
                box-sizing:border-box;
                width:0.92rem;
                height:2.62rem;
                margin-right:0.24rem;
                padding:0 0 0.36rem 0.36rem;
                img{
                    width:0.56rem;
                    height:0.56rem;
                    border-radius:1rem;
                }
            }
            // 右部
            .rightpart{
                box-sizing:border-box;
                width:5.96rem;
                // 用户名和时间
                .nametime{
                    height:0.24rem;
                    box-sizing:border-box;
                    font-size:0.2rem;
                    line-height:0.24rem;
                    margin-bottom:0.08rem;
                    // 用户名
                    span:first-child{
                        color:rgb(7,17,27);
                        float:left;
                    }
                    // 时间
                    span:last-child{
                        color:rgb(147,153,159);
                        font-weight:200;
                        float:right;
                    }
                }
                .below{
                    width:5.8rem;
                    box-sizing:border-box;
                // 星级
                    .level{
                        box-sizing:border-box;
                        height:0.24rem;
                        margin-bottom:0.12rem;
                        display:flex;
                        .stars{
                            width:1.24rem;
                            margin-right:0.12rem;
                            img{
                                width:0.2rem;
                                height:0.2rem;
                            }  
                        }
                        // 送达时间
                        span{
                            color:rgb(147,153,159);
                            font-weight:200;
                            font-size:0.2rem;
                            line-height:0.24rem;
                            box-sizing:border-box;
                        }
                    }
                    // 评语
                    .comment{
                        color:rgb(7,17,27);
                        font-weight:200;
                        font-size:0.24rem;
                        line-height:0.36rem;
                        box-sizing:border-box;
                        margin-bottom:0.16rem;
                    }
                    // 赞
                    .praise{
                        font-size:0.24rem;
                        line-height:0.32rem;
                        // height:0.32rem;
                        box-sizing:border-box;
                        display: flex;
                        p{
                            width:0.22rem;
                            height:0.2rem;
                            font-size:0.24rem;
                            color:rgb(0,160,220);
                            line-height:0.32rem;
                            // float: left;
                            img{
                                width:0.24rem;
                                height: 0.24rem;
                            }
                        }
                        >div{
                            // float: left;
                            width: 5.5rem;
                            display: flex;
                            overflow: hidden;
                            flex-wrap: wrap;
                            margin-left: 0.16rem;
                            text-align: center;
                            // white-space: nowrap;
                            // text-overflow: ellipsis;
                            .item{
                                width:1.26rem;
                                font-size:0.18rem;
                                color:rgb(147,153,159);
                                line-height:0.32rem;
                                border:1px solid rgba(7,17,27,0.1);
                                border-radius:0.02rem;
                                background-color:rgb(255,255,255);
                                padding:0 0.12rem;
                                margin:0 0.16rem 0.1rem 0;
                                overflow: hidden;
                                white-space: nowrap;
                                text-overflow: ellipsis;

                            }
                        }                       
                    }
                }
            }
        }
    }
</style>


