import axios from 'axios';

const state={
    goods:[],
    detailJusty : false,
    food : {},
    foodcar:[],
    showChoice1:false
}

const getters={
    goods(state){
        return state.goods;
    },
    detailJusty( state ){
		return state.detailJusty;
	},
	food( state ){
		return state.food;
    },
    showChoice1( state ){
		return state.showChoice1;
    },
    foodcar( state ){
		return state.foodcar;
    }
}

const actions={
    getGoods({commit}){
        axios.get('../../../data.json').then( res => {
            // console.log(res.data.goods)
            commit ('getGoods',res.data.goods);
        })
    },
    showgoodsDetail( { commit } , food ){
        // console.log(food);
		commit( 'showgoodsDetail' ,food );
    },
    showChoice({commit},foodcar){
        // console.log(foodcar);
        commit( 'showChoice' ,foodcar );
    },
    clearcar({commit},foodcar){
        commit( 'clearcar' ,foodcar );
    }
}

const mutations={
    getGoods(state,data){
        state.goods=data;
    },
    showgoodsDetail(state,food){
		state.detailJusty = true;
		state.food = food;
    },
    showChoice(state,foodcar){
        // console.log(123);
        state.showChoice1 = true;
		state.foodcar = foodcar;
    },
    clearcar(state,foodcar){
        // console.log(1)
        state.foodcar=[];
    }
}

export default{
    state,
    getters,
    mutations,
    actions
}


