<template>
    <div class="shopCar">
        
        <div class="left">
            <!-- 图片 -->
            <div class="pic" @click='showChoice(selectFoods)'>
                <div class="bigcir" :class='{"bluebg":showChoice1}'>
                    <div v-show='showChoice1' class="countred"><span>{{totalCount}}</span></div>
                    <img v-show='!showChoice1' src="../../../static/imgs/car.png">
                    <img v-show='showChoice1' src="../../../static/imgs/car1.png">
                </div>
            </div>
            <!-- 价格 -->
            <div class="price" :class='{"heightlight" : totalCount > 0}'>¥{{totalPrice}}</div>
        </div>
        <div class="middle">
            <p>另需配送费¥4元</p>
        </div>
        <div class="right">
            <p>{{payDesc}}</p>
        </div>
        <div class="choice" v-show="showChoice1">
            <goodchoices :foodcar='foodcar'></goodchoices>
        </div>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    import BScroll from 'better-scroll'
    import goodchoices from '../goodChoices/goodChioces.vue'

    export default{
        components:{
            goodchoices
        },
        computed:{
             ...mapGetters([
                'goods',
                'seller',
                'showChoice1',
                'foodcar'
            ]),
            selectFoods(){
                let foods = [];
                // 遍历大的分类
                this.goods.forEach( good => {
                    // 遍历分类里面的每一个商品
                    good.foods.forEach( food => {
                        // 如果商品的count大于0  就放进定义的数组中去
                        if( food.count > 0 ){
                            foods.push(food);
                        }
                    })
                });
                // dom是数据的自然映射，数据改变则dom随之更新
                return foods;
            },
            totalPrice(){
                let total = 0;
                // 对上面的计算属性进行遍历（就是对数组遍历，里面的每一个对象都有count）
                this.selectFoods.forEach( food => {
                    total += (food.count * food.price)+4;
                });
                return total;
            },
            totalCount(){
                let count = 0 ;
                this.selectFoods.forEach( food => {
                    count += food.count;
                });
                return count;
            },
            payDesc(){
                if( this.totalPrice === 0){
                    return `￥${this.seller.minPrice}元起送`;
                }else if( this.totalPrice < this.seller.minPrice ){
                    return `还差￥${this.seller.minPrice - this.totalPrice}元起送`
                }else{
                    return "去结算";
                }
            }
        },
        methods:{
            // 显示详情页面
            showChoice(foodcar){
                this.$store.dispatch('showChoice',foodcar);
            },

        } 
    }
</script>

<style lang="less" scoped>
    .shopCar{
        width: 100%;
        height: 1rem;
        background-color: #141d27;
        padding-bottom:0.16rem;
        display: flex;
        z-index: 100;
        justify-content: space-between;
        .left{
            width: 2.3rem;
            height: 1.16rem;
            padding-top: 0.22rem;
            box-sizing: border-box;
            .pic{
                left: 0.36rem;
                position: absolute;
                bottom: 0.16rem;
                .bigcir{
                    box-sizing: border-box;
                    background-color: #2b343d;
                    // background-color: #00a0dc;
                    // border: 1px solid white;
                    width: 1.12rem;
                    height: 1.12rem;
                    border-radius: 1rem;
                    border: 0.1rem solid #141d27;
                    &.bluebg{
                        background-color: #00a0dc;
                    }
                    .countred{
                        width: 0.48rem;
                        height: 0.32rem;
                        background:#f00f0d;
                        position: absolute;
                        border-radius: 0.2rem;
                        top: 0;
                        left: 0.6rem;
                        span{
                            text-align: center;
                            color: rgb(255, 255, 255);
                            line-height: 0.32rem;
                            font-size: 0.18rem;
                            font-weight: 700;
                            padding: 0.12rem;
                        }
                    }
                    img{
                        width: 0.48rem;
                        height: 0.48rem;
                        margin: 0.2rem;
                    }
                }
            }
            .price{
                width: 0.9rem;
                box-sizing: border-box;
                border-right:1px solid rgba(255, 255, 255, 0.1); 
                font-size: 0.32rem;
                color: rgba(255, 255, 255,0.4);
                font-weight: 700;
                line-height: 0.48rem;
                float: right;
                // padding-top: 0.24rem;
                text-align: center;
                vertical-align: middle;
            }
        }
        .middle{
            // display: inline-block;
            font-size: 0.24rem;
            color: rgba(255, 255, 255, 0.4);
            line-height: 0.48rem;
            padding-top: 0.22rem;
            // vertical-align: middle;
        }
        .right{
            // display: inline-block;
            padding: 0.22rem 0.16rem 0;
            width: 2.1rem;
            font-size: 0.24rem;
            color: rgba(255, 255, 255, 0.4);
            font-weight: 700;
            line-height: 0.48rem;
            text-align: center;
            background-color: #2b343b;
        }
        .choice{
            // height:6.11rem;
            height: 100%;
            position: absolute;
            bottom: 3rem;
            width: 100%;
            z-index: -9;
        }
    }
</style>









